<template>
  <pagina
    :loading="loading"
    :modal="modal"
    subtitulo="Pgina de Manuteno de Licenas"
    titulo="Licenas"
    :mais-opcoes="formulario.id ? maisOpcoes : null"
    :titulo-formulario="controle.editar ? 'Editar Registro' : controle.inserir ? 'Adicionar Registro' : 'Exibir Registro'"
    @excluir="excluirRegistro()"
    @voltar="resetFormulario()"
  >
    <template slot="listagem">
      <v-form @submit.prevent="''">
        <v-container
          class="my-0 py-0"
          fluid
        >
          <v-row dense>
            <v-col cols="12">
              <filtro
                :options="optionsFilter"
                @clearFilters="limparFiltros()"
                @adicionar="controle.inserir = true, modal = true, formulario.status_licenca_id === enumStatusLicenca.digitacao"
                @pesquisar="listarRegistro()"
              >
                <template slot="filtros">
                  <v-container
                    class="my-0 py-0"
                    fluid
                  >
                    <v-row dense>
                      <v-col
                        xl="1"
                        lg="1"
                        md="2"
                        sm="4"
                        cols="12"
                      >
                        <v-text-field
                          v-model="filtro.id"
                          hide-details
                          dense
                          label="Cdigo"
                          outlined
                        />
                      </v-col>
                      <v-col
                        xl="2"
                        lg="2"
                        md="3"
                        sm="8"
                        cols="12"
                      >
                        <v-text-field
                          v-model="filtro.cnpj"
                          v-mask="'##.###.###/####-##'"
                          hide-details
                          dense
                          label="CNPJ"
                          outlined
                        />
                      </v-col>
                      <v-col
                        xl="2"
                        lg="2"
                        md="3"
                        sm="4"
                        cols="12"
                      >
                        <selecao-all
                          v-model="filtro.status"
                          :items="dropdownStatusLicencas"
                          hide-details
                          dense
                          item-value="item"
                          item-text="descricao"
                          label="Status"
                          outlined
                        />
                      </v-col>
                      <v-col
                        xl="3"
                        lg="3"
                        md="4"
                        sm="8"
                        cols="12"
                      >
                        <selecao-all
                          v-model="filtro.tipo"
                          :items="dropdownTiposLicencas"
                          hide-details
                          dense
                          item-value="item"
                          item-text="descricao"
                          label="Tipo Licena"
                          outlined
                        />
                      </v-col>

                      <v-col
                        xl="2"
                        lg="2"
                        md="4"
                        sm="6"
                        cols="12"
                      >
                        <v-text-field
                          v-model="filtro.processo"
                          v-mask="'##.#####.##/####'"
                          hide-details
                          dense
                          label="Processo"
                          outlined
                        />
                      </v-col>
                      <v-col
                        xl="2"
                        lg="2"
                        md="3"
                        sm="6"
                        cols="12"
                      >
                        <v-text-field
                          v-model="filtro.dataVencimento"
                          v-mask="'##/##/####'"
                          hide-details
                          dense
                          label="Data de Vencimento"
                          outlined
                        />
                      </v-col>
                      <v-col
                        xl="12"
                        lg="12"
                        md="5"
                        sm="12"
                        cols="12"
                      >
                        <v-text-field
                          v-model="filtro.razaoSocial"
                          v-uppercase
                          hide-details
                          dense
                          label="Razo Social"
                          outlined
                        />
                      </v-col>
                    </v-row>
                  </v-container>
                </template>
              </filtro>
            </v-col>
            <v-col cols="12">
              <tabela
                :colunas="colunas"
                :registros="registros"
                :paginacao="paginacao"
                :registros-por-pagina="100"
                :sort-by-cli="['id']"
                :sort-desc-cli="true"
                height-auto
                exibir
                class="mt-2"
                toolbar-grid
                titulo="Listagem de Licenas"
                @paginacao="paginacao = $event"
                @exibir="exibirRegistro($event)"
              />
            </v-col>
          </v-row>
        </v-container>
      </v-form>
    </template>

    <template slot="formulario">
      <v-form @submit.prevent="''">
        <validation-observer ref="observer">
          <v-container
            fluid
            grid-list-xs
          >
            <v-row dense>
              <v-col
                v-if="formulario.id"
                xl="1"
                lg="2"
                md="3"
                sm="4"
                cols="12"
              >
                <v-text-field
                  v-model="formulario.id"
                  hide-details
                  disabled
                  dense
                  label="Cdigo Licena"
                  outlined
                />
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="3"
                sm="4"
                cols="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Processo"
                  vid="processo"
                  rules="required"
                >
                  <v-text-field
                    v-model="formulario.processo"
                    v-mask="'##.#####.##/####'"
                    :error-messages="errors"
                    :hide-details="!errors.length"
                    :disabled="controle.exibir"
                    dense
                    label="Processo"
                    class="required"
                    outlined
                  />
                </validation-provider>
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="3"
                sm="4"
                cols="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Licena"
                  vid="Licena"
                  rules="max:45"
                >
                  <v-text-field
                    v-model="formulario.licenca"
                    :error-messages="errors"
                    :hide-details="!errors.length"
                    :disabled="controle.exibir"
                    dense
                    label="Licena"
                    outlined
                  />
                </validation-provider>
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="3"
                sm="4"
                cols="12"
              >
                <v-autocomplete
                  v-model="formulario.status_licenca_id"
                  disabled
                  :items="dropdownStatusLicencas"
                  hide-details
                  dense
                  item-value="item"
                  item-text="descricao"
                  label="Status Licena"
                  outlined
                />
              </v-col>
              <v-col
                :xl="formulario.id ? 3 : 2"
                :lg="formulario.id ? 4 : 4"
                :md="formulario.id ? 6 : 3"
                :sm="formulario.id ? 8 : 8"
                cols="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Tipo Licena"
                  vid="tipo"
                  rules="required"
                >
                  <v-autocomplete
                    v-model="formulario.tipo_licenca_id"
                    :error-messages="errors"
                    :hide-details="!errors.length"
                    :disabled="controle.exibir"
                    :items="dropdownTiposLicencas"
                    dense
                    item-value="item"
                    item-text="descricao"
                    label="Tipo Licena"
                    class="required"
                    outlined
                  />
                </validation-provider>
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="3"
                sm="4"
                cols="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Data de Vencimento"
                  vid="dataVencimento"
                  rules="required"
                >
                  <v-text-field
                    v-model="formulario.data_vencimento"
                    v-mask="'##/##/####'"
                    :error-messages="errors"
                    :hide-details="!errors.length"
                    :disabled="controle.exibir"
                    dense
                    label="Data de Vencimento"
                    class="required"
                    outlined
                  />
                </validation-provider>
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="3"
                sm="4"
                cols="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Data de Sada"
                  vid="dataSaida"
                  rules=""
                >
                  <v-text-field
                    v-model="formulario.data_saida"
                    v-mask="'##/##/####'"
                    :error-messages="errors"
                    :hide-details="!errors.length"
                    :disabled="controle.exibir"
                    dense
                    label="Data de Sada"
                    class=""
                    outlined
                  />
                </validation-provider>
              </v-col>
              <v-col
                xl="2"
                lg="3"
                md="3"
                sm="4"
                cols="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Estado"
                  vid="estado"
                  rules="required"
                >
                  <v-autocomplete
                    v-model="formulario.estado"
                    :items="dropdownEstados"
                    :disabled="controle.exibir"
                    :error-messages="errors"
                    :hide-details="!errors.length"
                    dense
                    item-value="uf"
                    item-text="nome"
                    label="Estado"
                    class="required"
                    outlined
                  />
                </validation-provider>
              </v-col>
              <v-col
                xl="2"
                lg="3"
                md="3"
                sm="4"
                cols="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Cidade"
                  vid="cidade"
                  rules="required"
                >
                  <v-autocomplete
                    v-model="formulario.cidade"
                    :items="dropdownCidades"
                    :disabled="controle.exibir || !formulario.estado || !(formulario.estado === 'RO' || formulario.estado === 'AM' || formulario.estado === 'AC')"
                    :error-messages="errors"
                    :hide-details="!errors.length"
                    dense
                    item-value="codigo"
                    item-text="nome"
                    label="Cidade"
                    class="required"
                    outlined
                  />
                </validation-provider>
              </v-col>
              <v-col
                cols="12"
                xl="12"
                lg="12"
                md="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Observao"
                  rules="max:500"
                  vid="observacao"
                >
                  <v-textarea
                    v-model="formulario.observacao"
                    v-uppercase
                    :disabled="!controle.inserir && !controle.editar"
                    :error-messages="errors"
                    :hide-details="!(errors.length || (formulario.observacao && formulario.observacao.length > 0) && !controle.exibir)"
                    :counter="500"
                    dense
                    label="Observao"
                    outlined
                    rows="3"
                    spellcheck="false"
                  />
                </validation-provider>
              </v-col>
              <v-col
                cols="12"
              >
                Empresa
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="2"
                sm="4"
                cols="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Empresa"
                  vid="Empresa"
                  rules="required"
                >
                  <v-text-field
                    v-model="formulario.empresa_id"
                    :error-messages="errors"
                    :hide-details="!errors.length"
                    :disabled="controle.exibir"
                    append-icon="mdi-magnify"
                    dense
                    label="Empresa (clique na lupa)"
                    outlined
                    class="required"
                    @click:append="modalBuscarEmpresa = true"
                  />
                </validation-provider>
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="3"
                sm="4"
                cols="12"
              >
                <v-text-field
                  v-model="formulario.cnpj"
                  v-mask="'##.###.###/####-##'"
                  disabled
                  hide-details
                  dense
                  label="CNPJ"
                  outlined
                />
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="3"
                sm="4"
                cols="12"
              >
                <v-autocomplete
                  v-model="formulario.status_empresa_id"
                  disabled
                  :items="dropdownStatusEmpresa"
                  hide-details
                  dense
                  item-value="item"
                  item-text="descricao"
                  label="Status Empresa"
                  outlined
                />
              </v-col>
              <v-col
                xl="6"
                lg="6"
                md="4"
                sm="6"
                cols="12"
              >
                <v-text-field
                  v-model="formulario.nome_fantasia"
                  v-uppercase
                  disabled
                  hide-details
                  dense
                  label="Nome Fantasia"
                  outlined
                />
              </v-col>
              <v-col
                xl="3"
                lg="3"
                md="5"
                sm="6"
                cols="12"
              >
                <v-text-field
                  v-model="formulario.razao_social"
                  v-uppercase
                  disabled
                  hide-details
                  dense
                  label="Razo Social"
                  outlined
                />
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="2"
                sm="4"
                cols="12"
              >
                <v-text-field
                  v-model="formulario.data_cadastro"
                  v-mask="'##/##/####'"
                  disabled
                  hide-details
                  dense
                  label="Data de Cadastro"
                  outlined
                />
              </v-col>
              <v-col
                xl="3"
                lg="3"
                md="5"
                sm="8"
                cols="12"
              >
                <v-autocomplete
                  v-model="formulario.porte_empresa_id"
                  :items="dropdownPortesEmpresa"
                  hide-details
                  disabled
                  dense
                  item-value="item"
                  item-text="descricao"
                  label="Porte"
                  outlined
                />
              </v-col>
              <v-col
                xl="4"
                lg="4"
                md="12"
                sm="12"
                cols="12"
              >
                <v-text-field
                  v-model="formulario.empresaEndereco"
                  disabled
                  hide-details
                  dense
                  label="Endereo Empresa"
                  outlined
                />
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="6"
                sm="6"
                cols="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Estado Empresa"
                  rules="required"
                  vid="estadoEmpresa"
                >
                  <v-autocomplete
                    v-model="formulario.estado_empresa"
                    :items="dropdownEstados"
                    :error-messages="errors"
                    :hide-details="!errors.length"
                    disabled
                    dense
                    item-value="uf"
                    item-text="nome"
                    label="Estado"
                    outlined
                  />
                </validation-provider>
              </v-col>
              <v-col
                xl="2"
                lg="2"
                md="6"
                sm="6"
                cols="12"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="Cidade Empresa"
                  rules="required"
                  vid="cidadeEmpresa"
                >
                  <v-autocomplete
                    v-model="formulario.cidade_empresa"
                    :items="dropdownCidadesEmpresa"
                    :error-messages="errors"
                    :hide-details="!errors.length"
                    disabled
                    dense
                    item-value="codigo"
                    item-text="nome"
                    label="Cidade"
                    outlined
                  />
                </validation-provider>
              </v-col>
              <v-col
                cols="12"
              >
                <v-divider />
              </v-col>
              <v-col
                v-if="formulario.id"
                xl="3"
                lg="3"
                md="3"
                sm="3"
                cols="12"
              >
                <v-text-field
                  v-model="formulario.created_by"
                  hide-details
                  disabled
                  dense
                  label="Criado Por"
                  outlined
                />
              </v-col>
              <v-col
                v-if="formulario.id"
                xl="3"
                lg="3"
                md="3"
                sm="3"
                cols="12"
              >
                <v-text-field
                  v-model="formulario.created_at"
                  hide-details
                  disabled
                  dense
                  label="Criado Em"
                  outlined
                />
              </v-col>
            </v-row>
          </v-container>
        </validation-observer>
      </v-form>
    </template>

    <template slot="botoes">
      <v-btn
        v-if="formulario.id"
        :block="$vuetify.breakpoint.xsOnly"
        :class="$vuetify.breakpoint.xsOnly ? 'my-1' : 'mx-1'"
        color="warning"
        small
        @click="$notificacao('Fluxo ainda em desenvolvimento', 'erro')"
      >
        <v-icon
          left
          size="20"
        >
          mdi-text
        </v-icon>
        Gerar RMAs
      </v-btn>
      <v-btn
        v-if="!!(!controle.exibir && (controle.inserir || controle.editar))"
        :block="$vuetify.breakpoint.xsOnly"
        :class="$vuetify.breakpoint.xsOnly ? 'my-1' : 'mx-1'"
        color="success"
        small
        @click="salvarRegistro()"
      >
        <v-icon
          left
          size="20"
        >
          mdi-content-save
        </v-icon>
        Salvar
      </v-btn>
      <v-btn
        v-if="!!(controle.exibir && !controle.inserir)"
        :block="$vuetify.breakpoint.xsOnly"
        :class="$vuetify.breakpoint.xsOnly ? 'my-1' : 'mx-1'"
        color="success"
        small
        @click="controle.editar = true, controle.exibir = false"
      >
        <v-icon
          left
          size="20"
        >
          mdi-pencil
        </v-icon>
        Editar
      </v-btn>
      <v-btn
        :block="$vuetify.breakpoint.xsOnly"
        :class="$vuetify.breakpoint.xsOnly ? 'my-1' : 'mx-1'"
        color="error"
        small
        @click="modal = false, resetFormulario()"
      >
        <v-icon
          left
          size="20"
        >
          mdi-cancel
        </v-icon>
        Voltar
      </v-btn>
    </template>

    <modal
      v-model="modalBuscarEmpresa"
      width="100%"
      :titulo="'Pesquisar Empresa'"
      :mais-opcoes="false"
      @fechar="resetModalEmpresa()"
    >
      <template>
        <v-form @submit.prevent="''">
          <v-container
            class="ma-0 pa-0"
            fluid
          >
            <v-row dense>
              <v-col cols="12">
                <filtro
                  :options="optionsFilterModalBuscarEmpresa"
                  @clearFilters="limparFiltrosModalEmpresa()"
                  @pesquisar="listarRegistroEmpresas()"
                >
                  <template slot="filtros">
                    <v-container
                      class="my-0 py-0"
                      fluid
                    >
                      <v-row dense>
                        <v-col
                          xl="1"
                          lg="1"
                          md="4"
                          sm="4"
                          cols="12"
                        >
                          <v-text-field
                            v-model="filtroModalEmpresa.id"
                            v-mask="'###########'"
                            hide-details
                            dense
                            label="Cdigo"
                            outlined
                          />
                        </v-col>
                        <v-col
                          xl="2"
                          lg="2"
                          md="4"
                          sm="4"
                          cols="12"
                        >
                          <v-text-field
                            v-model="filtroModalEmpresa.cnpj"
                            v-mask="'##.###.###/####-##'"
                            hide-details
                            dense
                            label="CNPJ"
                            outlined
                          />
                        </v-col>
                        <v-col
                          xl="2"
                          lg="2"
                          md="4"
                          sm="4"
                          cols="12"
                        >
                          <selecao-all
                            v-model="filtroModalEmpresa.status"
                            :items="dropdownStatusEmpresa"
                            hide-details
                            dense
                            item-value="item"
                            item-text="descricao"
                            label="Status"
                            outlined
                          />
                        </v-col>
                        <v-col
                          xl="2"
                          lg="2"
                          md="4"
                          sm="12"
                          cols="12"
                        >
                          <v-text-field
                            v-model="filtroModalEmpresa.nomeFantasia"
                            v-uppercase
                            hide-details
                            dense
                            label="Nome Fantasia"
                            outlined
                          />
                        </v-col>
                        <v-col
                          xl="3"
                          lg="3"
                          md="4"
                          sm="12"
                          cols="12"
                        >
                          <v-text-field
                            v-model="filtroModalEmpresa.razaoSocial"
                            v-uppercase
                            hide-details
                            dense
                            label="Razo Social"
                            outlined
                          />
                        </v-col>
                        <v-col
                          xl="2"
                          lg="2"
                          md="4"
                          sm="12"
                          cols="12"
                        >
                          <selecao-all
                            v-model="filtroModalEmpresa.porte"
                            :items="dropdownPortesEmpresa"
                            hide-details
                            dense
                            item-value="item"
                            item-text="descricao"
                            label="Porte"
                            outlined
                          />
                        </v-col>
                      </v-row>
                    </v-container>
                  </template>
                </filtro>
              </v-col>
              <v-col cols="12">
                <tabela
                  :colunas="colunasEmpresa"
                  :registros="registrosEmpresas"
                  :paginacao="paginacaoEmpresas"
                  :registros-por-pagina="100"
                  :sort-by-cli="['id']"
                  :sort-desc-cli="true"
                  height-auto
                  class="mt-2"
                  toolbar-grid
                  titulo="Listagem de Empresas"
                  escolher
                  @paginacao="paginacaoEmpresas = $event"
                  @escolher="resetModalEmpresa(), exibirRegistroEmpresa($event)"
                />
              </v-col>
            </v-row>
          </v-container>
        </v-form>
      </template>
    </modal>
  </pagina>
</template>

<script>
import { mapActions, mapState, mapMutations } from 'vuex'

export default {
  name: 'TelaLicencas',
  data: () => ({
    loading: false,
    modalBuscarEmpresa: false,
    colunasEmpresa: [
      {
        text: 'Ao',
        align: 'start',
        sortable: false,
        value: 'acao'
      },
      {
        text: 'Cdigo',
        align: 'start',
        sortable: false,
        value: 'id'
      },
      {
        text: 'CNPJ',
        align: 'start',
        sortable: false,
        value: 'cnpj'
      },
      {
        text: 'Status',
        align: 'start',
        sortable: false,
        value: 'status'
      },
      {
        text: 'Nome Fantasia',
        align: 'start',
        sortable: false,
        value: 'nome_fantasia'
      },
      {
        text: 'Razo Social',
        align: 'start',
        sortable: false,
        value: 'razao_social'
      },
      {
        text: 'Data Cadastro',
        align: 'start',
        sortable: false,
        value: 'data_cadastro'
      },
      {
        text: 'Porte',
        align: 'start',
        sortable: false,
        value: 'porte_descricao'
      }
    ],
    colunas: [
      {
        text: 'Ao',
        align: 'start',
        sortable: false,
        value: 'acao'
      },
      {
        text: 'Cdigo',
        align: 'start',
        sortable: false,
        value: 'id'
      },
      {
        text: 'Status',
        align: 'start',
        sortable: false,
        value: 'status'
      },
      {
        text: 'CNPJ Empresa',
        align: 'start',
        sortable: false,
        value: 'cnpj'
      },
      {
        text: 'Razo Social',
        align: 'start',
        sortable: false,
        value: 'razao_social'
      },
      {
        text: 'Licena',
        align: 'start',
        sortable: false,
        value: 'licenca'
      },
      {
        text: 'N. Processo',
        align: 'start',
        sortable: false,
        value: 'processo'
      },
      {
        text: 'Vencimento',
        align: 'start',
        sortable: false,
        value: 'data_vencimento'
      },
      {
        text: 'Tipo Lincena',
        align: 'start',
        sortable: false,
        value: 'tipo'
      },
      {
        text: 'Criado Por',
        align: 'start',
        sortable: false,
        value: 'created_by'
      },
      {
        text: 'Criado Em',
        align: 'start',
        sortable: false,
        value: 'created_at'
      }
    ],
    filtroModalEmpresa: {
      id: null,
      cnpj: null,
      status: [],
      nomeFantasia: null,
      razaoSocial: null,
      porte: []
    },
    filtro: {
      id: null,
      cnpj: null,
      status: null,
      tipo: null,
      razaoSocial: null,
      processo: null,
      dataVencimento: null
    },
    controle: {
      exibir: false,
      editar: false,
      inserir: false
    },
    formulario: {
      bairro: null,
      cep: null,
      cidade: null,
      cidade_empresa: null,
      cnpj: null,
      complemento: null,
      created_at: null,
      created_by: null,
      data_cadastro: null,
      data_saida: null,
      data_vencimento: null,
      empresa_id: null,
      estado: null,
      estado_empresa: null,
      id: null,
      inscricao_estadual: null,
      inscricao_municipal: null,
      licenca: null,
      logradouro: null,
      nome_fantasia: null,
      numero: null,
      observacao: null,
      porte_empresa_id: null,
      processo: null,
      razao_social: null,
      status_empresa_id: null,
      status_licenca_id: null,
      tipo_licenca_id: null,
      updated_at: null,
      updated_by: null
    },
    formularioEmpresa: {
      id: null,
      cnpj: null,
      status: null,
      nomeFantasia: null,
      razaoSocial: null,
      dataCadastro: null,
      porte: null,
      empresaEndereco: null,
      estado: null,
      cidade: null,
      created_at: null,
      created_by: null
    },
    enumStatusLicenca: {
      digitacao: 1
    },
    paginacao: {
      pagina: 1,
      registros: 100,
      totalRegistros: 0
    },
    paginacaoEmpresas: {
      pagina: 1,
      registros: 100,
      totalRegistros: 0
    },
    modal: false
  }),
  computed: {
    ...mapState('licencas', [
      'registros',
      'dropdownStatusLicencas',
      'dropdownTiposLicencas',
      'registrosEmpresas',
      'dropdownStatusEmpresa',
      'dropdownPortesEmpresa',
      'dropdownEstados',
      'dropdownCidadesEmpresa',
      'dropdownCidades'
    ]),
    optionsFilterModalBuscarEmpresa () {
      return {
        adicionar: false,
        values: !!(
          this.filtroModalEmpresa.id ||
          this.filtroModalEmpresa.cnpj ||
          (this.filtroModalEmpresa.status
            ? this.filtroModalEmpresa.status.length
            : null) ||
          this.filtroModalEmpresa.nomeFantasia ||
          this.filtroModalEmpresa.razaoSocial ||
          (this.filtroModalEmpresa.porte
            ? this.filtroModalEmpresa.porte.length
            : null)
        )
      }
    },
    optionsFilter () {
      return {
        adicionar: true,
        values: !!(
          this.filtro.id ||
          this.filtro.cnpj ||
          (this.filtro.status ? this.filtro.status.length : null) ||
          (this.filtro.tipo ? this.filtro.tipo.length : null) ||
          this.filtro.razaoSocial ||
          this.filtro.processo ||
          this.filtro.dataVencimento
        )
      }
    },
    maisOpcoes () {
      return [
        {
          acao: 'excluir',
          color: 'error',
          icone: 'mdi-delete',
          titulo: 'Excluir'
        }
      ]
    }
  },
  watch: {
    async 'formularioEmpresa.estado' (value) {
      if (value && (this.formularioEmpresa.estado === 'RO' || this.formularioEmpresa.estado === 'AM' || this.formularioEmpresa.estado === 'AC')) await this.buscarDropdownCidadeEmpresa(value)
    },
    async 'formulario.estado' (value) {
      if (value && (this.formulario.estado === 'RO' || this.formulario.estado === 'AM' || this.formulario.estado === 'AC')) await this.buscarDropdownCidade(value)
    },
    async 'formulario.estado_empresa' (value) {
      if (value && (this.formulario.estado_empresa === 'RO' || this.formulario.estado_empresa === 'AM' || this.formulario.estado_empresa === 'AC')) await this.buscarDropdownCidadeEmpresa(value)
    },
    'formulario.empresaId' (value) {
      if (!value) {
        this.formularioEmpresa = {
          id: null,
          cnpj: null,
          status: null,
          nomeFantasia: null,
          razaoSocial: null,
          dataCadastro: null,
          porte: null,
          empresaEndereco: null,
          estado: null,
          cidade: null,
          created_at: null,
          created_by: null
        }
      }
    }
  },
  async created () {
    if (this.$route.query && this.$route.query.id) {
      this.filtro.id = this.$route.query.id
    }
    await this.buscarDropdownTiposLicencas()
    await this.buscarDropdownStatusLicencas()
    await this.buscarDropdownPortesEmpresa()
    await this.buscarDropdownStatusEmpresa()
    await this.buscarDropdownEstados()
    this.listarRegistro()
    this.listarRegistroEmpresas()
  },
  methods: {
    ...mapMutations('licencas', [
      'setRegistrosEmpresas'
    ]),
    ...mapActions('licencas', [
      'listar',
      'exibir',
      'salvar',
      'editar',
      'excluir',
      'buscarDropdownStatusLicencas',
      'buscarDropdownTiposLicencas',

      'listarEmpresas',
      'exibirEmpresas',
      'buscarDropdownPortesEmpresa',
      'buscarDropdownStatusEmpresa',
      'buscarDropdownEstados',
      'buscarDropdownCidade',
      'buscarDropdownCidadeEmpresa'
    ]),
    async listarRegistro () {
      this.loading = true
      await this.listar({
        id: this.filtro.id || null,
        cnpj: this.filtro.cnpj ? String(this.filtro.cnpj).match(/\d/g).join('') : null,
        status: this.filtro.status || null,
        tipo: this.filtro.tipo || null,
        razaoSocial: this.filtro.razaoSocial || null,
        processo: this.filtro.processo ? String(this.filtro.processo).match(/\d/g).join('') : null,
        dataVencimento: this.filtro.dataVencimento ? this.$day(this.filtro.dataVencimento, 'DD/MM/YYYY').format('YYYY-MM-DD') : null
      })
      this.loading = false
    },
    async exibirRegistro (registro) {
      this.loading = true
      const res = await this.exibir(registro)
      if (res && !res.erro) {
        this.formulario = {
          bairro: res.bairro || null,
          cep: res.cep || null,
          cidade: res.cidade || null,
          cidade_empresa: res.cidade_empresa || null,
          cnpj: res.cnpj || null,
          complemento: res.complemento || null,
          created_at: res.created_at ? this.$day(res.created_at).format('DD/MM/YYYY HH:mm:ss') : null,
          created_by: res.created_by || null,
          data_cadastro: res.data_cadastro ? this.$day(res.data_cadastro).format('DD/MM/YYYY') : null,
          data_saida: res.data_saida ? this.$day(res.data_saida).format('DD/MM/YYYY') : null,
          data_vencimento: res.data_vencimento ? this.$day(res.data_vencimento).format('DD/MM/YYYY') : null,
          empresa_id: res.empresa_id || null,
          estado: res.estado || null,
          estado_empresa: res.estado_empresa || null,
          id: res.id || null,
          inscricao_estadual: res.inscricao_estadual || null,
          inscricao_municipal: res.inscricao_municipal || null,
          logradouro: res.logradouro || null,
          licenca: res.licenca || null,
          nome_fantasia: res.nome_fantasia || null,
          numero: res.numero || null,
          observacao: res.observacao || null,
          porte_empresa_id: res.porte_empresa_id || null,
          processo: res.processo || null,
          razao_social: res.razao_social || null,
          status_empresa_id: res.status_empresa_id || null,
          status_licenca_id: res.status_licenca_id || null,
          tipo_licenca_id: res.tipo_licenca_id || null,
          updated_at: res.updated_at || null,
          updated_by: res.updated_by || null
        }
      }
      this.formulario.empresaEndereco = `${res.logradouro}, ${res.numero} - ${res.bairro}`
      this.loading = false
      this.modal = true
      this.controle.exibir = true
    },
    async salvarRegistro () {
      if (await this.$refs.observer.validate()) {
        this.loading = true
        const form = {
          id: this.formulario.id || null,
          tipo: this.formulario.tipo_licenca_id || null,
          processo: this.formulario.processo ? String(this.formulario.processo).match(/\d/g).join('') : null,
          licenca: this.formulario.licenca || null,
          dataVencimento: this.formulario.data_vencimento ? this.$day(this.formulario.data_vencimento, 'DD/MM/YYYY').format('YYYY-MM-DD') : null,
          dataSaida: this.formulario.data_saida ? this.$day(this.formulario.data_saida, 'DD/MM/YYYY').format('YYYY-MM-DD') : null,
          empresaId: this.formulario.empresa_id || null,
          estado: this.formulario.estado || null,
          cidade: this.formulario.cidade || null,
          observacao: this.formulario.observacao || null
        }

        let res
        if (form.id) res = await this.editar(form)
        else res = await this.salvar(form)

        if (res && !res.erro) {
          this.modal = false
          this.resetFormulario()
          this.exibirRegistro(res.id)
        }
        this.loading = false
      }
    },
    async excluirRegistro () {
      this.loading = true
      const res = await this.excluir(this.formulario.id)
      if (res && !res.erro) {
        this.modal = false
        this.resetFormulario()
      }
      this.loading = false
    },
    // EMPRESA MODAL
    async listarRegistroEmpresas () {
      this.loading = true
      await this.listarEmpresas({
        id: this.filtroModalEmpresa.id || null,
        cnpj: this.filtroModalEmpresa.cnpj ? String(this.filtroModalEmpresa.cnpj).match(/\d/g).join('') : undefined,
        status: this.filtroModalEmpresa.status && this.filtroModalEmpresa.status.length ? this.filtroModalEmpresa.status : null,
        nomeFantasia: this.filtroModalEmpresa.nomeFantasia || null,
        razaoSocial: this.filtroModalEmpresa.razaoSocial || null,
        porteEmpresa: this.filtroModalEmpresa.porte && this.filtroModalEmpresa.porte.length ? this.filtroModalEmpresa.porte : null
      })
      this.loading = false
    },
    async exibirRegistroEmpresa (id) {
      this.loading = true
      const res = await this.exibirEmpresas(id)
      if (res && !res.erro) {
        const formularioEmpresa = {
          empresa_id: res.id || null,
          cnpj: res.cnpj || null,
          status_empresa_id: res.status_empresa_id || null,
          nome_fantasia: res.nome_fantasia || null,
          razao_social: res.razao_social || null,
          data_cadastro: res.data_cadastro ? this.$day(res.data_cadastro).format('DD/MM/YYYY') : null,
          porte_empresa_id: res.porte_empresa_id || null,
          empresaEndereco: `${res.logradouro}, ${res.numero} - ${res.estado} ${res.bairro} ${res.cep}`,
          estado_empresa: res.estado || null,
          cidade_empresa: res.cidade || null
        }
        this.formulario = { ...this.formulario, ...formularioEmpresa }

        this.formulario.empresaId = res.id
      }
      this.loading = false
    },
    async resetFormulario () {
      this.loading = true
      this.modal = false
      this.controle = {
        exibir: null,
        inserir: null,
        editar: null
      }
      this.formulario = {
        bairro: null,
        cep: null,
        cidade: null,
        cidade_empresa: null,
        cnpj: null,
        complemento: null,
        created_at: null,
        created_by: null,
        data_cadastro: null,
        data_saida: null,
        data_vencimento: null,
        empresa_id: null,
        estado: null,
        estado_empresa: null,
        id: null,
        inscricao_estadual: null,
        inscricao_municipal: null,
        logradouro: null,
        licenca: null,
        nome_fantasia: null,
        numero: null,
        observacao: null,
        porte_empresa_id: null,
        processo: null,
        razao_social: null,
        status_empresa_id: null,
        status_licenca_id: null,
        tipo_licenca_id: null,
        updated_at: null,
        updated_by: null
      }
      this.loading = false
    },
    resetModalEmpresa () {
      this.modalBuscarEmpresa = false
      this.setRegistrosEmpresas([])
      this.filtroModalEmpresa = {
        id: null,
        cnpj: null,
        status: [],
        nomeFantasia: null,
        razaoSocial: null,
        porte: []
      }
    },
    limparFiltros () {
      this.filtro = {
        id: null,
        cnpj: null,
        status: null,
        tipo: null,
        razaoSocial: null,
        processo: null,
        dataVencimento: null
      }
    },
    limparFiltrosModalEmpresa () {
      this.filtro = {
        id: null,
        cnpj: null,
        status: [],
        nomeFantasia: null,
        razaoSocial: null,
        porte: []
      }
    }
  }
}
</script>
